import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Col, Row, Table } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAllAction, getSortState, IPaginationBaseState, getPaginationItemsNumber, JhiPagination } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntities } from './disponibilidad-competencias.reducer';
import { IDisponibilidadCompetencias } from 'app/shared/model/disponibilidad-competencias.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';
import { ITEMS_PER_PAGE } from 'app/shared/util/pagination.constants';

export interface IDisponibilidadCompetenciasProps extends StateProps, DispatchProps, RouteComponentProps<{ url: string }> {}

export type IDisponibilidadCompetenciasState = IPaginationBaseState;

export class DisponibilidadCompetencias extends React.Component<IDisponibilidadCompetenciasProps, IDisponibilidadCompetenciasState> {
  state: IDisponibilidadCompetenciasState = {
    ...getSortState(this.props.location, ITEMS_PER_PAGE)
  };

  componentDidMount() {
    this.getEntities();
  }

  sort = prop => () => {
    this.setState(
      {
        order: this.state.order === 'asc' ? 'desc' : 'asc',
        sort: prop
      },
      () => this.sortEntities()
    );
  };

  sortEntities() {
    this.getEntities();
    this.props.history.push(`${this.props.location.pathname}?page=${this.state.activePage}&sort=${this.state.sort},${this.state.order}`);
  }

  handlePagination = activePage => this.setState({ activePage }, () => this.sortEntities());

  getEntities = () => {
    const { activePage, itemsPerPage, sort, order } = this.state;
    this.props.getEntities(activePage - 1, itemsPerPage, `${sort},${order}`);
  };

  render() {
    const { disponibilidadCompetenciasList, match, totalItems } = this.props;
    return (
      <div>
        <h2 id="disponibilidad-competencias-heading">
          <Translate contentKey="ghostceetApp.disponibilidadCompetencias.home.title">Disponibilidad Competencias</Translate>
          <Link to={`${match.url}/new`} className="btn btn-primary float-right jh-create-entity" id="jh-create-entity">
            <FontAwesomeIcon icon="plus" />
            &nbsp;
            <Translate contentKey="ghostceetApp.disponibilidadCompetencias.home.createLabel">
              Create new Disponibilidad Competencias
            </Translate>
          </Link>
        </h2>
        <div className="table-responsive">
          <Table responsive>
            <thead>
              <tr>
                <th className="hand" onClick={this.sort('id')}>
                  <Translate contentKey="global.field.id">ID</Translate> <FontAwesomeIcon icon="sort" />
                </th>
                <th>
                  <Translate contentKey="ghostceetApp.disponibilidadCompetencias.instructor">Instructor</Translate>{' '}
                  <FontAwesomeIcon icon="sort" />
                </th>
                <th>
                  <Translate contentKey="ghostceetApp.disponibilidadCompetencias.vinculacionInstructor">Vinculacion Instructor</Translate>{' '}
                  <FontAwesomeIcon icon="sort" />
                </th>
                <th />
              </tr>
            </thead>
            <tbody>
              {disponibilidadCompetenciasList.map((disponibilidadCompetencias, i) => (
                <tr key={`entity-${i}`}>
                  <td>
                    <Button tag={Link} to={`${match.url}/${disponibilidadCompetencias.id}`} color="link" size="sm">
                      {disponibilidadCompetencias.id}
                    </Button>
                  </td>
                  <td>
                    {disponibilidadCompetencias.instructor ? (
                      <Link to={`instructor/${disponibilidadCompetencias.instructor.id}`}>{disponibilidadCompetencias.instructor.id}</Link>
                    ) : (
                      ''
                    )}
                  </td>
                  <td>
                    {disponibilidadCompetencias.vinculacionInstructor ? (
                      <Link to={`vinculacion-instructor/${disponibilidadCompetencias.vinculacionInstructor.id}`}>
                        {disponibilidadCompetencias.vinculacionInstructor.id}
                      </Link>
                    ) : (
                      ''
                    )}
                  </td>
                  <td className="text-right">
                    <div className="btn-group flex-btn-group-container">
                      <Button tag={Link} to={`${match.url}/${disponibilidadCompetencias.id}`} color="info" size="sm">
                        <FontAwesomeIcon icon="eye" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.view">View</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${disponibilidadCompetencias.id}/edit`} color="primary" size="sm">
                        <FontAwesomeIcon icon="pencil-alt" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.edit">Edit</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${disponibilidadCompetencias.id}/delete`} color="danger" size="sm">
                        <FontAwesomeIcon icon="trash" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.delete">Delete</Translate>
                        </span>
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
        <Row className="justify-content-center">
          <JhiPagination
            items={getPaginationItemsNumber(totalItems, this.state.itemsPerPage)}
            activePage={this.state.activePage}
            onSelect={this.handlePagination}
            maxButtons={5}
          />
        </Row>
      </div>
    );
  }
}

const mapStateToProps = ({ disponibilidadCompetencias }: IRootState) => ({
  disponibilidadCompetenciasList: disponibilidadCompetencias.entities,
  totalItems: disponibilidadCompetencias.totalItems
});

const mapDispatchToProps = {
  getEntities
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DisponibilidadCompetencias);
