import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Col, Row, Table } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAllAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntities } from './ficha-trimestre.reducer';
import { IFichaTrimestre } from 'app/shared/model/ficha-trimestre.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IFichaTrimestreProps extends StateProps, DispatchProps, RouteComponentProps<{ url: string }> {}

export class FichaTrimestre extends React.Component<IFichaTrimestreProps> {
  componentDidMount() {
    this.props.getEntities();
  }

  render() {
    const { fichaTrimestreList, match } = this.props;
    return (
      <div>
        <h2 id="ficha-trimestre-heading">
          <Translate contentKey="ghostceetApp.fichaTrimestre.home.title">Ficha Trimestres</Translate>
          <Link to={`${match.url}/new`} className="btn btn-primary float-right jh-create-entity" id="jh-create-entity">
            <FontAwesomeIcon icon="plus" />
            &nbsp;
            <Translate contentKey="ghostceetApp.fichaTrimestre.home.createLabel">Create new Ficha Trimestre</Translate>
          </Link>
        </h2>
        <div className="table-responsive">
          <Table responsive>
            <thead>
              <tr>
                <th>
                  <Translate contentKey="global.field.id">ID</Translate>
                </th>
                <th>
                  <Translate contentKey="ghostceetApp.fichaTrimestre.ficha">Ficha</Translate>
                </th>
                <th />
              </tr>
            </thead>
            <tbody>
              {fichaTrimestreList.map((fichaTrimestre, i) => (
                <tr key={`entity-${i}`}>
                  <td>
                    <Button tag={Link} to={`${match.url}/${fichaTrimestre.id}`} color="link" size="sm">
                      {fichaTrimestre.id}
                    </Button>
                  </td>
                  <td>{fichaTrimestre.ficha ? <Link to={`ficha/${fichaTrimestre.ficha.id}`}>{fichaTrimestre.ficha.numero}</Link> : ''}</td>
                  <td className="text-right">
                    <div className="btn-group flex-btn-group-container">
                      <Button tag={Link} to={`${match.url}/${fichaTrimestre.id}`} color="info" size="sm">
                        <FontAwesomeIcon icon="eye" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.view">View</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${fichaTrimestre.id}/edit`} color="primary" size="sm">
                        <FontAwesomeIcon icon="pencil-alt" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.edit">Edit</Translate>
                        </span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${fichaTrimestre.id}/delete`} color="danger" size="sm">
                        <FontAwesomeIcon icon="trash" />{' '}
                        <span className="d-none d-md-inline">
                          <Translate contentKey="entity.action.delete">Delete</Translate>
                        </span>
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ fichaTrimestre }: IRootState) => ({
  fichaTrimestreList: fichaTrimestre.entities
});

const mapDispatchToProps = {
  getEntities
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FichaTrimestre);
