import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './ficha-trimestre.reducer';
import { IFichaTrimestre } from 'app/shared/model/ficha-trimestre.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IFichaTrimestreDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: string }> {}

export class FichaTrimestreDetail extends React.Component<IFichaTrimestreDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { fichaTrimestreEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            <Translate contentKey="ghostceetApp.fichaTrimestre.detail.title">FichaTrimestre</Translate> [<b>{fichaTrimestreEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <Translate contentKey="ghostceetApp.fichaTrimestre.ficha">Ficha</Translate>
            </dt>
            <dd>{fichaTrimestreEntity.ficha ? fichaTrimestreEntity.ficha.numero : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/ficha-trimestre" replace color="info">
            <FontAwesomeIcon icon="arrow-left" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.back">Back</Translate>
            </span>
          </Button>
          &nbsp;
          <Button tag={Link} to={`/entity/ficha-trimestre/${fichaTrimestreEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.edit">Edit</Translate>
            </span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ fichaTrimestre }: IRootState) => ({
  fichaTrimestreEntity: fichaTrimestre.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FichaTrimestreDetail);
