import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './modalidad.reducer';
import { IModalidad } from 'app/shared/model/modalidad.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IModalidadDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: string }> {}

export class ModalidadDetail extends React.Component<IModalidadDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { modalidadEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            <Translate contentKey="ghostceetApp.modalidad.detail.title">Modalidad</Translate> [<b>{modalidadEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <span id="nombre">
                <Translate contentKey="ghostceetApp.modalidad.nombre">Nombre</Translate>
              </span>
            </dt>
            <dd>{modalidadEntity.nombre}</dd>
            <dt>
              <span id="estado">
                <Translate contentKey="ghostceetApp.modalidad.estado">Estado</Translate>
              </span>
            </dt>
            <dd>{modalidadEntity.estado}</dd>
            <dt>
              <span id="color">
                <Translate contentKey="ghostceetApp.modalidad.color">Color</Translate>
              </span>
            </dt>
            <dd>{modalidadEntity.color}</dd>
          </dl>
          <Button tag={Link} to="/entity/modalidad" replace color="info">
            <FontAwesomeIcon icon="arrow-left" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.back">Back</Translate>
            </span>
          </Button>
          &nbsp;
          <Button tag={Link} to={`/entity/modalidad/${modalidadEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.edit">Edit</Translate>
            </span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ modalidad }: IRootState) => ({
  modalidadEntity: modalidad.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ModalidadDetail);
