import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { Translate, ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './planeacion-actividad.reducer';
import { IPlaneacionActividad } from 'app/shared/model/planeacion-actividad.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPlaneacionActividadDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: string }> {}

export class PlaneacionActividadDetail extends React.Component<IPlaneacionActividadDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { planeacionActividadEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            <Translate contentKey="ghostceetApp.planeacionActividad.detail.title">PlaneacionActividad</Translate> [
            <b>{planeacionActividadEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <Translate contentKey="ghostceetApp.planeacionActividad.planeacionTrimestre">Planeacion Trimestre</Translate>
            </dt>
            <dd>{planeacionActividadEntity.planeacionTrimestre ? planeacionActividadEntity.planeacionTrimestre.id : ''}</dd>
            <dt>
              <Translate contentKey="ghostceetApp.planeacionActividad.actividadProyecto">Actividad Proyecto</Translate>
            </dt>
            <dd>{planeacionActividadEntity.actividadProyecto ? planeacionActividadEntity.actividadProyecto.id : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/planeacion-actividad" replace color="info">
            <FontAwesomeIcon icon="arrow-left" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.back">Back</Translate>
            </span>
          </Button>
          &nbsp;
          <Button tag={Link} to={`/entity/planeacion-actividad/${planeacionActividadEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" />{' '}
            <span className="d-none d-md-inline">
              <Translate contentKey="entity.action.edit">Edit</Translate>
            </span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ planeacionActividad }: IRootState) => ({
  planeacionActividadEntity: planeacionActividad.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PlaneacionActividadDetail);
